import { execFile } from "child_process";
import { NextResponse } from "next/server";
import path from "path";

export async function POST(req) {
  const userCode = await req.text();

  // Full path of parser.exe inside /bin folder
  const exePath = path.join(process.cwd(), "bin", "parser.exe");

  return new Promise((resolve) => {
    const p = execFile(exePath, [], (err, stdout, stderr) => {
      if (err) {
        resolve(NextResponse.json({ error: stderr }));
      } else {
        resolve(NextResponse.json({ output: stdout }));
      }
    });

    // Send text to parser.exe input
    p.stdin.write(userCode);
    p.stdin.end();
  });
}
